@import './theme/dark.css';
@import './theme/light.css';
@import './reset.css';

.App {
  background: var(--background-color);
  color: var(--text-color);

  width: 100%;
  max-height: 100vh;
}

.container {
  max-width: 1250px;
  margin: auto;
}

.pokemonCard {
  width: 300px;
  height: 500px;

  color: var(--text-color-card);
  background: var(--background-card);
  border: 1px solid var(--border-card);

  border-radius: 8px;

  display: flex;
  flex-direction: column;
  align-items: center;

  text-transform: uppercase;
}

.listOfPokemons {
  display: grid;
  align-items: center;
  justify-content: center;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
}

.searchWrapper {
  width: 100%;

  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 15px;
  margin-bottom: 20px;
}

.searchInput {
  padding: 8px 16px;
  font-size: 24px;
  border-radius: 8px;
  text-align: center;
  width: 300px;

  border: 1px solid var(--input-border);
  background: var(--input-background);
  color: var(--input-text);

  transition: all 0.3s ease;
}
.searchInput:focus {
  width: 450px;
}

.searchButton {
  padding: 8px 12px;
  font-size: 24px;
  border-radius: 8px;
  text-align: center;

  border: 1px solid var(--input-border);
  background: var(--input-background);
  color: var(--input-text);

  cursor: pointer;

  transition: all 0.3s ease;
}
.searchButton:hover {
  color: green;
  
}

.pokemonImg {
  width: 250px;
  height: 250px;
}
.pokemonName {
  font-size: 26px;
  font-weight: 700;
  margin-bottom: 20px;
}

.container .oneColomn:first-child {
  position: fixed;
  height: 40px;
  width: 40px;
  background-color: transparent;
  right: 0;
  top: 0;
  z-index: 999999;
  text-transform: uppercase;
  border-radius: 50%;
  border: none;
  background: var(--input-background);
  color: var(--input-text);
  font-size: 30px;
  transition: .4s ease;
}
.container .oneColomn:first-child:hover {
  background-color: #3a3a3a;
}
.container .oneColomn:nth-child(2) {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  height: 40px;
  width: 100px;
  background-color: rgb(148, 57, 57);
  right: 0;
  bottom: 190px;
  z-index: 999999;
  text-transform: uppercase;
  border: none;
  color: #fff;
}
.container .oneColomn:nth-child(3){
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  height: 40px;
  width: 100px;
  background-color: rgb(148, 57, 57);
  right: 0;
  bottom: 150px;
  z-index: 999999;
  text-transform: uppercase;
  color: #fff;
  border: none;
}

.listStats {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}
.pokemonStats {
  width: 70%;
  display: flex;
  justify-content: space-between;
  text-transform: capitalize;

}

.paginationWrapper {
  margin-bottom: 500px;
  width: 100%;
  display: flex;
  justify-content: space-around;
}

.loader {
  position: absolute;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  background: #3a3a3a;

  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 50px;
  color: #fff;
}

.searchWrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}